import { Component, OnInit } from '@angular/core';
import { ConferenceService } from '../shared/services/conference-service/conference.service';
import { IConferenceModel } from '../shared/Models/conference.model';

@Component({
  selector: 'app-conferences',
  templateUrl: './conferences.component.html',
  styleUrls: ['./conferences.component.css']
})
export class ConferencesComponent implements OnInit {

  confenceList: IConferenceModel[] = [];
  errorMessage : any;

  constructor(private conferenceService: ConferenceService) { }

  ngOnInit() {
    this.getAllConferences();
  }

  getAllConferences(){
    this.conferenceService.getAllConferences().subscribe(
      conferencesFound => {
        this.confenceList = conferencesFound;  
      },
      error => this.errorMessage = <any>error
    );
  }

}
