import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ConferenceModel, IConferenceModel } from '../../Models/conference.model';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ConferenceService {

  private conferenceUrl = "assets/api/conferences/conference.json";

  constructor(private http: HttpClient) { }

  getAllConferences(): Observable<IConferenceModel[]> {
    return this.http.get<IConferenceModel[]>(this.conferenceUrl);
  }

  getConferenceById(conferenceId:string): Observable<IConferenceModel> {
    return this.getAllConferences().pipe( map(conferences => conferences.find(conference => conference.ConferenceId == conferenceId )));
  }
}
