import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IDocument } from '../../Models/document.model';
import { HttpHeaders, HttpErrorResponse, HttpClient } from '@angular/common/http';
import { tap, catchError, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DocumentService {

  private documentsUrl = "assets/api/documents/documents.json";

  constructor(private http: HttpClient) { }

  getDocuments(): Observable<IDocument[]> {
    let reqHeader = new HttpHeaders({'No-Auth':'True'})
    return this.http.get<IDocument[]>(this.documentsUrl,{headers : reqHeader}).pipe(
      tap(data => console.log('All: ' + JSON.stringify(data))),
      catchError(this.handleError)
    );
  }

  getDocumentById(id: number): Observable<IDocument | undefined> {
    return this.getDocuments().pipe(
      map((products: IDocument[]) => products.find(p => p.Id === id))
    );
  }

  addNewDocument(addNewDocument: any)
  {
    console.log("Document saved Mr Gee")
    let reqHeader = new HttpHeaders({'No-Auth':'True'})
    return this.http.post('http:localhost/api/document', addNewDocument, {headers : reqHeader});
  }

  private handleError(err: HttpErrorResponse) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    let errorMessage = '';
    if (err.error instanceof Error) {
      // A client-side or network error occurred. Handle it accordingly.
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return Observable.throw(errorMessage);
  }

}

